import router from "./index";
import store from "@/store";
import { Message } from "element-ui";
router.beforeEach((to, from, next) => {
  if (to.name !== "login") {
    // 
    if (store.getters.token !== null && store.getters.token !== "") {
      // 
      next();
    } else {
      // 
      Message({
        type: "warning",
        message: ""
      });
      next({ path: "/login" });
    }
  } else {
    next();
  }
});
