// 
const baseSize = 16
//  rem 
function setRem() {
  //  1920 
  const scale = Math.max((document.documentElement.clientWidth || document.body.clientWidth) / 1920, 0.5)
  // 
  document.documentElement.style.fontSize = (baseSize * Math.min(scale, 3)) + 'px'
  document.body.style.fontSize = (baseSize * Math.min(scale, 3)) + 'px'
}
// 
setRem()
//  rem
window.onresize = function () {
  setRem()
}